.bak {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 4px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: background-color 0.3s, opacity 0.5s, visibility 0.5s;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}

.bak::after {
  content: "\f077";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  font-size: 2em;
  color: #fff;
}

.bak:hover {
  cursor: pointer;
  background-color: #29c0cc;
}

.bak:active {
  background-color: #1b0b70;
}

.bak.show {
  opacity: 1;
  visibility: visible;
}
